<template>

  <head>
    <meta charset="utf-8">
    <title>App Login </title>
  </head>

  <body>
    <div class="wrapper">
      <div class="title">
        Login Form
      </div>
      <form action="#">
        <div class="field">
          <input type="email" required v-model="email">
          <label>Email Address</label>
        </div>
        <div class="field">
          <input type="password" required v-model="password">
          <label>Password</label>
        </div>

        <div class="field">
          <input type="login" value="Login" readonly @click="onLogin">
        </div>
        Not a user?
        <div class="field">
          <input type="login" @click="goToRegister" readonly value="Register">
        </div>

      </form>
    </div>
  </body>
</template>

<script setup>
import { ref } from 'vue';
import { useRouter } from 'vue-router';
import axios from 'axios';
import { useStore } from 'vuex';
const store = useStore();
const router = useRouter();

const email = ref("");
const password = ref("");


function goToRegister() {
  router.push('/registerPage');
}

function onLogin() {

  const url = "http://127.0.0.1:5000/login";
  const data = {
    email: email.value,
    password: password.value
  };

  const data_sent_config = {
    headers: {
      'Content-Type': "application/json"
    },
  }

  axios.post(url, data, data_sent_config).then((res) => {
    if (res.data.role == "END_USER") {

      localStorage.setItem('token', res.data.access_token);
      store.commit("setUserData", res.data)

      localStorage.setItem('First_Name', JSON.stringify(res.data.First_Name));
      localStorage.setItem('user_name', JSON.stringify(res.data.user_name));
      localStorage.setItem('user_id', JSON.stringify(res.data.id));
      localStorage.setItem('Last_Name', JSON.stringify(res.data.Last_Name));

      router.push(`/userDash/${res.data.user_name}`);
    }
    if (res.data.role == "ADMIN") {
      localStorage.setItem('token', res.data.access_token);
      router.push('/adDash');
    }
  }).catch((err) => {
    alert(err.response.data.message)
    console.log(err);
  })

}
</script>

